export interface Alimento {
  id: string
  nombre: string
  categoria: 'proteinas' | 'carbohidratos' | 'grasas' | 'vegetales' | 'frutas' | 'lacteos' | 'cereales' | 'legumbres' | 'frutos-secos' | 'bebidas'
  calorias: number // por 100g
  proteinas: number // gramos por 100g
  carbohidratos: number // gramos por 100g
  grasas: number // gramos por 100g
  fibra: number // gramos por 100g
  azucares?: number // gramos por 100g
  sodio?: number // mg por 100g
  unidadMedida: 'gramos' | 'mililitros' | 'unidad' | 'taza' | 'cucharada'
  pesoPromedio?: number // para unidades (ej: 1 manzana = 150g)
  descripcion?: string
}

