// Funciones de utilidad
export const obtenerAlimentosPorCategoria = (categoria: string) => {
  return alimentosBase.filter(alimento => alimento.categoria === categoria)
}

export const buscarAlimentos = (termino: string) => {
  const terminoLower = termino.toLowerCase()
  return alimentosBase.filter(alimento => 
    alimento.nombre.toLowerCase().includes(terminoLower) ||
    alimento.categoria.toLowerCase().includes(terminoLower)
  )
}

export const obtenerAlimentoPorId = (id: string) => {
  return alimentosBase.find(alimento => alimento.id === id)
}

export const calcularMacronutrientes = (alimento: Alimento, cantidad: number) => {
  let factorConversion = 1

  // Convertir cantidad según la unidad de medida
  if (alimento.unidadMedida === 'unidad' && alimento.pesoPromedio) {
    factorConversion = (cantidad * alimento.pesoPromedio) / 100
  } else if (alimento.unidadMedida === 'taza') {
    // Aproximación: 1 taza = 240ml o 150g dependiendo del alimento
    factorConversion = (cantidad * 150) / 100
  } else if (alimento.unidadMedida === 'cucharada') {
    // Aproximación: 1 cucharada = 15ml o 10g
    factorConversion = (cantidad * 10) / 100
  } else {
    // Para gramos y mililitros
    factorConversion = cantidad / 100
  }

  return {
    calorias: Math.round(alimento.calorias * factorConversion),
    proteinas: Math.round(alimento.proteinas * factorConversion * 10) / 10,
    carbohidratos: Math.round(alimento.carbohidratos * factorConversion * 10) / 10,
    grasas: Math.round(alimento.grasas * factorConversion * 10) / 10,
    fibra: Math.round(alimento.fibra * factorConversion * 10) / 10
  }
}

export const obtenerObjetivosCaloricos = (objetivo: 'perdida' | 'mantenimiento' | 'ganancia', peso: number, altura: number, edad: number, sexo: 'hombre' | 'mujer', actividad: 'sedentario' | 'ligero' | 'moderado' | 'intenso') => {
  // Cálculo de TMB (Tasa Metabólica Basal) usando fórmula Mifflin-St Jeor
  let tmb
  if (sexo === 'hombre') {
    tmb = 10 * peso + 6.25 * altura - 5 * edad + 5
  } else {
    tmb = 10 * peso + 6.25 * altura - 5 * edad - 161
  }

  // Factor de actividad
  const factoresActividad = {
    sedentario: 1.2,
    ligero: 1.375,
    moderado: 1.55,
    intenso: 1.725
  }

  const caloriasMantenim = tmb * factoresActividad[actividad]

  // Ajustar según objetivo
  let caloriasObjetivo
  switch (objetivo) {
    case 'perdida':
      caloriasObjetivo = caloriasMantenim - 500 // Déficit de 500 cal
      break
    case 'ganancia':
      caloriasObjetivo = caloriasMantenim + 300 // Superávit de 300 cal
      break
    default:
      caloriasObjetivo = caloriasMantenim
  }

  // Distribución de macronutrientes (ejemplo estándar)
  const proteinasPorKg = objetivo === 'ganancia' ? 2.2 : 1.8
  const proteinasGramos = peso * proteinasPorKg
  const proteinasCalorias = proteinasGramos * 4

  const grasasPorcentaje = 0.25
  const grasasCalorias = caloriasObjetivo * grasasPorcentaje
  const grasasGramos = grasasCalorias / 9

  const carbohidratosCalorias = caloriasObjetivo - proteinasCalorias - grasasCalorias
  const carbohidratosGramos = carbohidratosCalorias / 4

  return {
    calorias: Math.round(caloriasObjetivo),
    proteinas: Math.round(proteinasGramos),
    carbohidratos: Math.round(carbohidratosGramos),
    grasas: Math.round(grasasGramos)
  }
}
