'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Progress } from '@/components/ui/progress'
import FormularioAlimentos from '@/components/FormularioAlimentos'

interface AlimentoConsumido {
  id: string
  alimento: any
  cantidad: number
  comida: 'desayuno' | 'almuerzo' | 'cena' | 'snack'
  fecha: string
  calorias: number
  proteinas: number
  carbohidratos: number
  grasas: number
  fibra: number
}

interface ResumenDiario {
  calorias: number
  proteinas: number
  carbohidratos: number
  grasas: number
  fibra: number
}

interface ObjetivosDiarios {
  calorias: number
  proteinas: number
  carbohidratos: number
  grasas: number
}

export default function PaginaAlimentos() {
  const [alimentosHoy, setAlimentosHoy] = useState<AlimentoConsumido[]>([])
  const [mostrarFormulario, setMostrarFormulario] = useState(false)
  const [alimentoEditando, setAlimentoEditando] = useState<AlimentoConsumido | null>(null)
  const [resumenDiario, setResumenDiario] = useState<ResumenDiario>({
    calorias: 0,
    proteinas: 0,
    carbohidratos: 0,
    grasas: 0,
    fibra: 0
  })
  const [objetivos] = useState<ObjetivosDiarios>({
    calorias: 2000,
    proteinas: 150,
    carbohidratos: 250,
    grasas: 67
  })

  useEffect(() => {
    cargarAlimentosHoy()
  }, [])

  useEffect(() => {
    calcularResumenDiario()
  }, [alimentosHoy])

  const cargarAlimentosHoy = () => {
    try {
      const hoy = new Date().toDateString()
      const alimentosGuardados = JSON.parse(localStorage.getItem('alimentosConsumidos') || '[]')
      const alimentosHoyFiltrados = alimentosGuardados.filter((alimento: AlimentoConsumido) => 
        new Date(alimento.fecha).toDateString() === hoy
      )
      setAlimentosHoy(alimentosHoyFiltrados)
    } catch (error) {
      console.error('Error cargando alimentos:', error)
    }
  }

  const calcularResumenDiario = () => {
    const resumen = alimentosHoy.reduce((acc, alimento) => ({
      calorias: acc.calorias + alimento.calorias,
      proteinas: acc.proteinas + alimento.proteinas,
      carbohidratos: acc.carbohidratos + alimento.carbohidratos,
      grasas: acc.grasas + alimento.grasas,
      fibra: acc.fibra + alimento.fibra
    }), {
      calorias: 0,
      proteinas: 0,
      carbohidratos: 0,
      grasas: 0,
      fibra: 0
    })

    setResumenDiario(resumen)
  }

  const guardarAlimento = (alimento: AlimentoConsumido) => {
    try {
      const alimentosGuardados = JSON.parse(localStorage.getItem('alimentosConsumidos') || '[]')
      
      let alimentosActualizados
      if (alimentoEditando) {
        // Actualizar alimento existente
        alimentosActualizados = alimentosGuardados.map((a: AlimentoConsumido) => 
          a.id === alimento.id ? alimento : a
        )
      } else {
        // Agregar nuevo alimento
        alimentosActualizados = [...alimentosGuardados, alimento]
      }

      localStorage.setItem('alimentosConsumidos', JSON.stringify(alimentosActualizados))
      
      setMostrarFormulario(false)
      setAlimentoEditando(null)
      cargarAlimentosHoy()
      
      alert('Alimento registrado exitosamente!')
    } catch (error) {
      console.error('Error guardando alimento:', error)
      alert('Error al guardar el alimento')
    }
  }

  const eliminarAlimento = (id: string) => {
    if (confirm('¿Estás seguro de que quieres eliminar este alimento?')) {
      try {
        const alimentosGuardados = JSON.parse(localStorage.getItem('alimentosConsumidos') || '[]')
        const alimentosActualizados = alimentosGuardados.filter((a: AlimentoConsumido) => a.id !== id)
        localStorage.setItem('alimentosConsumidos', JSON.stringify(alimentosActualizados))
        cargarAlimentosHoy()
      } catch (error) {
        console.error('Error eliminando alimento:', error)
      }
    }
  }

  const editarAlimento = (alimento: AlimentoConsumido) => {
    setAlimentoEditando(alimento)
    setMostrarFormulario(true)
  }

  const agruparPorComida = (alimentos: AlimentoConsumido[]) => {
    return alimentos.reduce((acc, alimento) => {
      if (!acc[alimento.comida]) {
        acc[alimento.comida] = []
      }
      acc[alimento.comida].push(alimento)
      return acc
    }, {} as Record<string, AlimentoConsumido[]>)
  }

  const obtenerNombreComida = (comida: string) => {
    const nombres = {
      desayuno: 'Desayuno',
      almuerzo: 'Almuerzo',
      cena: 'Cena',
      snack: 'Snack'
    }
    return nombres[comida as keyof typeof nombres] || comida
  }

  if (mostrarFormulario) {
    return (
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">
              {alimentoEditando ? 'Editar Alimento' : 'Registrar Alimento'}
            </h1>
            <p className="text-gray-600 mt-1">
              {alimentoEditando ? 'Modifica la información del alimento' : 'Agrega un alimento a tu registro diario'}
            </p>
          </div>
          <Button 
            variant="outline" 
            onClick={() => {
              setMostrarFormulario(false)
              setAlimentoEditando(null)
            }}
          >
            Volver a Alimentos
          </Button>
        </div>

        <FormularioAlimentos 
          onGuardar={guardarAlimento}
          alimentoInicial={alimentoEditando || undefined}
        />
      </div>
    )
  }

  const alimentosAgrupados = agruparPorComida(alimentosHoy)

  return (
    <div className="space-y-6">
      {/* Encabezado */}
      <div className="flex flex-col md:flex-row md:items-center md:justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Control Nutricional</h1>
          <p className="text-gray-600 mt-1">
            Registra y controla tu alimentación diaria
          </p>
        </div>
        <Button onClick={() => setMostrarFormulario(true)} className="mt-4 md:mt-0">
          Registrar Alimento
        </Button>
      </div>

      {/* Resumen de macronutrientes */}
      <Card>
        <CardHeader>
          <CardTitle>Resumen del Día</CardTitle>
          <CardDescription>
            Progreso hacia tus objetivos nutricionales diarios
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            {/* Calorías */}
            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <span className="text-sm font-medium">Calorías</span>
                <span className="text-sm text-gray-600">
                  {Math.round(resumenDiario.calorias)} / {objetivos.calorias}
                </span>
              </div>
              <Progress 
                value={Math.min((resumenDiario.calorias / objetivos.calorias) * 100, 100)} 
                className="h-2"
              />
              <div className="text-xs text-gray-500">
                {Math.round(objetivos.calorias - resumenDiario.calorias)} restantes
              </div>
            </div>

            {/* Proteínas */}
            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <span className="text-sm font-medium">Proteínas</span>
                <span className="text-sm text-gray-600">
                  {Math.round(resumenDiario.proteinas)}g / {objetivos.proteinas}g
                </span>
              </div>
              <Progress 
                value={Math.min((resumenDiario.proteinas / objetivos.proteinas) * 100, 100)} 
                className="h-2"
              />
              <div className="text-xs text-gray-500">
                {Math.round(objetivos.proteinas - resumenDiario.proteinas)}g restantes
              </div>
            </div>

            {/* Carbohidratos */}
            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <span className="text-sm font-medium">Carbohidratos</span>
                <span className="text-sm text-gray-600">
                  {Math.round(resumenDiario.carbohidratos)}g / {objetivos.carbohidratos}g
                </span>
              </div>
              <Progress 
                value={Math.min((resumenDiario.carbohidratos / objetivos.carbohidratos) * 100, 100)} 
                className="h-2"
              />
              <div className="text-xs text-gray-500">
                {Math.round(objetivos.carbohidratos - resumenDiario.carbohidratos)}g restantes
              </div>
            </div>

            {/* Grasas */}
            <div className="space-y-2">
              <div className="flex justify-between items-center">
                <span className="text-sm font-medium">Grasas</span>
                <span className="text-sm text-gray-600">
                  {Math.round(resumenDiario.grasas)}g / {objetivos.grasas}g
                </span>
              </div>
              <Progress 
                value={Math.min((resumenDiario.grasas / objetivos.grasas) * 100, 100)} 
                className="h-2"
              />
              <div className="text-xs text-gray-500">
                {Math.round(objetivos.grasas - resumenDiario.grasas)}g restantes
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      <Tabs defaultValue="hoy" className="space-y-6">
        <TabsList>
          <TabsTrigger value="hoy">Hoy ({alimentosHoy.length})</TabsTrigger>
          <TabsTrigger value="historial">Historial</TabsTrigger>
          <TabsTrigger value="objetivos">Objetivos</TabsTrigger>
        </TabsList>

        <TabsContent value="hoy" className="space-y-6">
          {alimentosHoy.length > 0 ? (
            <div className="space-y-6">
              {['desayuno', 'almuerzo', 'cena', 'snack'].map(comida => {
                const alimentosComida = alimentosAgrupados[comida] || []
                if (alimentosComida.length === 0) return null

                const totalCalorias = alimentosComida.reduce((sum: number, a: AlimentoConsumido) => sum + a.calorias, 0)

                return (
                  <Card key={comida}>
                    <CardHeader>
                      <div className="flex items-center justify-between">
                        <CardTitle className="text-lg">
                          {obtenerNombreComida(comida)}
                        </CardTitle>
                        <Badge variant="secondary">
                          {Math.round(totalCalorias)} calorías
                        </Badge>
                      </div>
                    </CardHeader>
                    <CardContent>
                      <div className="space-y-3">
                        {alimentosComida.map((alimento: AlimentoConsumido) => (
                          <div key={alimento.id} className="flex items-center justify-between p-3 border rounded-lg">
                            <div className="flex-1">
                              <h4 className="font-medium">{alimento.alimento.nombre}</h4>
                              <p className="text-sm text-gray-600">
                                {alimento.cantidad}g • {alimento.calorias} cal
                              </p>
                              <div className="flex gap-4 text-xs text-gray-500 mt-1">
                                <span>P: {Math.round(alimento.proteinas)}g</span>
                                <span>C: {Math.round(alimento.carbohidratos)}g</span>
                                <span>G: {Math.round(alimento.grasas)}g</span>
                              </div>
                            </div>
                            <div className="flex gap-2">
                              <Button 
                                size="sm" 
                                variant="outline"
                                onClick={() => editarAlimento(alimento)}
                              >
                                Editar
                              </Button>
                              <Button 
                                size="sm" 
                                variant="destructive"
                                onClick={() => eliminarAlimento(alimento.id)}
                              >
                                Eliminar
                              </Button>
                            </div>
                          </div>
                        ))}
                      </div>
                    </CardContent>
                  </Card>
                )
              })}
            </div>
          ) : (
            <Card>
              <CardContent className="text-center py-12">
                <div className="text-6xl mb-4">🍎</div>
                <h3 className="text-xl font-semibold mb-2">¡Registra tu primera comida!</h3>
                <p className="text-gray-600 mb-6">
                  Comienza a llevar un control detallado de tu alimentación
                </p>
                <Button onClick={() => setMostrarFormulario(true)}>
                  Registrar Alimento
                </Button>
              </CardContent>
            </Card>
          )}
        </TabsContent>

        <TabsContent value="historial">
          <Card>
            <CardHeader>
              <CardTitle>Historial Nutricional</CardTitle>
              <CardDescription>
                Próximamente: Visualiza tu progreso nutricional a lo largo del tiempo
              </CardDescription>
            </CardHeader>
            <CardContent className="text-center py-12">
              <div className="text-6xl mb-4">📊</div>
              <h3 className="text-xl font-semibold mb-2">Historial Detallado</h3>
              <p className="text-gray-600 mb-6">
                Esta funcionalidad estará disponible próximamente
              </p>
              <Button disabled>
                Próximamente
              </Button>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="objetivos">
          <Card>
            <CardHeader>
              <CardTitle>Configurar Objetivos</CardTitle>
              <CardDescription>
                Próximamente: Personaliza tus objetivos nutricionales
              </CardDescription>
            </CardHeader>
            <CardContent className="text-center py-12">
              <div className="text-6xl mb-4">🎯</div>
              <h3 className="text-xl font-semibold mb-2">Objetivos Personalizados</h3>
              <p className="text-gray-600 mb-6">
                Configura objetivos basados en tu peso, altura, edad y nivel de actividad
              </p>
              <Button disabled>
                Próximamente
              </Button>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
