'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'
import { alimentosBase, categorias, calcularMacronutrientes, type Alimento } from '@/lib/datos-alimentos'

interface AlimentoConsumido {
  id: string
  alimento: Alimento
  cantidad: number
  comida: 'desayuno' | 'almuerzo' | 'cena' | 'snack'
  fecha: string
  calorias: number
  proteinas: number
  carbohidratos: number
  grasas: number
  fibra: number
}

interface FormularioAlimentosProps {
  onGuardar: (alimento: AlimentoConsumido) => void
  alimentoInicial?: AlimentoConsumido
}

export default function FormularioAlimentos({ onGuardar, alimentoInicial }: FormularioAlimentosProps) {
  const [alimentoSeleccionado, setAlimentoSeleccionado] = useState<string>(alimentoInicial?.alimento.id || '')
  const [cantidad, setCantidad] = useState<string>(alimentoInicial?.cantidad.toString() || '100')
  const [comida, setComida] = useState<string>(alimentoInicial?.comida || 'desayuno')
  const [categoriaFiltro, setCategoriaFiltro] = useState<string>('todas')
  const [busqueda, setBusqueda] = useState('')
  const [macrosCalculados, setMacrosCalculados] = useState<any>(null)

  // Filtrar alimentos
  const alimentosFiltrados = alimentosBase.filter(alimento => {
    const coincideBusqueda = alimento.nombre.toLowerCase().includes(busqueda.toLowerCase())
    const coincideCategoria = categoriaFiltro === 'todas' || alimento.categoria === categoriaFiltro
    
    return coincideBusqueda && coincideCategoria
  })

  // Calcular macronutrientes cuando cambia el alimento o cantidad
  const calcularYMostrarMacros = () => {
    if (!alimentoSeleccionado || !cantidad) {
      setMacrosCalculados(null)
      return
    }

    const alimento = alimentosBase.find(a => a.id === alimentoSeleccionado)
    if (!alimento) return

    const cantidadNum = parseFloat(cantidad)
    if (isNaN(cantidadNum) || cantidadNum <= 0) {
      setMacrosCalculados(null)
      return
    }

    const macros = calcularMacronutrientes(alimento, cantidadNum)
    setMacrosCalculados({
      alimento,
      cantidad: cantidadNum,
      ...macros
    })
  }

  // Recalcular cuando cambian los valores
  useEffect(() => {
    calcularYMostrarMacros()
  }, [alimentoSeleccionado, cantidad])

  const guardarAlimento = () => {
    if (!alimentoSeleccionado) {
      alert('Por favor, selecciona un alimento')
      return
    }

    if (!cantidad || parseFloat(cantidad) <= 0) {
      alert('Por favor, ingresa una cantidad válida')
      return
    }

    const alimento = alimentosBase.find(a => a.id === alimentoSeleccionado)
    if (!alimento) return

    const cantidadNum = parseFloat(cantidad)
    const macros = calcularMacronutrientes(alimento, cantidadNum)

    const alimentoConsumido: AlimentoConsumido = {
      id: alimentoInicial?.id || Date.now().toString(),
      alimento,
      cantidad: cantidadNum,
      comida: comida as any,
      fecha: alimentoInicial?.fecha || new Date().toISOString(),
      ...macros
    }

    onGuardar(alimentoConsumido)
  }

  const obtenerUnidadDisplay = (alimento: Alimento) => {
    switch (alimento.unidadMedida) {
      case 'gramos': return 'g'
      case 'mililitros': return 'ml'
      case 'unidad': return 'unidad(es)'
      case 'taza': return 'taza(s)'
      case 'cucharada': return 'cucharada(s)'
      default: return alimento.unidadMedida
    }
  }

  return (
    <div className="space-y-6">
      {/* Búsqueda y filtros */}
      <Card>
        <CardHeader>
          <CardTitle>Buscar Alimento</CardTitle>
          <CardDescription>
            Encuentra el alimento que consumiste
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <Label htmlFor="busqueda">Buscar alimento</Label>
              <Input
                id="busqueda"
                value={busqueda}
                onChange={(e) => setBusqueda(e.target.value)}
                placeholder="Buscar por nombre..."
              />
            </div>
            <div>
              <Label htmlFor="categoria">Categoría</Label>
              <Select value={categoriaFiltro} onValueChange={setCategoriaFiltro}>
                <SelectTrigger>
                  <SelectValue placeholder="Seleccionar categoría" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="todas">Todas las categorías</SelectItem>
                  {categorias.map(categoria => (
                    <SelectItem key={categoria.id} value={categoria.id}>
                      {categoria.nombre}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Selección de alimento */}
      <Card>
        <CardHeader>
          <CardTitle>Seleccionar Alimento y Cantidad</CardTitle>
          <CardDescription>
            Elige el alimento y especifica la cantidad consumida
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <Label htmlFor="alimento">Alimento *</Label>
            <Select value={alimentoSeleccionado} onValueChange={(value) => {
              setAlimentoSeleccionado(value)
              setTimeout(calcularYMostrarMacros, 100)
            }}>
              <SelectTrigger>
                <SelectValue placeholder="Seleccionar alimento" />
              </SelectTrigger>
              <SelectContent>
                {alimentosFiltrados.map(alimento => (
                  <SelectItem key={alimento.id} value={alimento.id}>
                    <div className="flex items-center gap-2">
                      <span>{alimento.nombre}</span>
                      <Badge variant="secondary" className="text-xs">
                        {alimento.categoria}
                      </Badge>
                    </div>
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          {alimentoSeleccionado && (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <Label htmlFor="cantidad">
                  Cantidad * 
                  {(() => {
                    const alimento = alimentosBase.find(a => a.id === alimentoSeleccionado)
                    return alimento ? ` (${obtenerUnidadDisplay(alimento)})` : ''
                  })()}
                </Label>
                <Input
                  id="cantidad"
                  type="number"
                  min="0"
                  step="0.1"
                  value={cantidad}
                  onChange={(e) => {
                    setCantidad(e.target.value)
                    setTimeout(calcularYMostrarMacros, 100)
                  }}
                  placeholder="100"
                />
              </div>
              <div>
                <Label htmlFor="comida">Comida *</Label>
                <Select value={comida} onValueChange={setComida}>
                  <SelectTrigger>
                    <SelectValue placeholder="Seleccionar comida" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="desayuno">Desayuno</SelectItem>
                    <SelectItem value="almuerzo">Almuerzo</SelectItem>
                    <SelectItem value="cena">Cena</SelectItem>
                    <SelectItem value="snack">Snack</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Vista previa de macronutrientes */}
      {macrosCalculados && (
        <Card>
          <CardHeader>
            <CardTitle>Información Nutricional</CardTitle>
            <CardDescription>
              Valores calculados para {macrosCalculados.cantidad} {obtenerUnidadDisplay(macrosCalculados.alimento)} de {macrosCalculados.alimento.nombre}
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
              <div className="text-center">
                <div className="text-2xl font-bold text-blue-600">
                  {macrosCalculados.calorias}
                </div>
                <div className="text-sm text-gray-600">Calorías</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-red-600">
                  {macrosCalculados.proteinas}g
                </div>
                <div className="text-sm text-gray-600">Proteínas</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-yellow-600">
                  {macrosCalculados.carbohidratos}g
                </div>
                <div className="text-sm text-gray-600">Carbohidratos</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-green-600">
                  {macrosCalculados.grasas}g
                </div>
                <div className="text-sm text-gray-600">Grasas</div>
              </div>
              <div className="text-center">
                <div className="text-2xl font-bold text-purple-600">
                  {macrosCalculados.fibra}g
                </div>
                <div className="text-sm text-gray-600">Fibra</div>
              </div>
            </div>

            <Separator className="my-4" />

            <div className="text-sm text-gray-600">
              <p><strong>Descripción:</strong> {macrosCalculados.alimento.descripcion || 'Sin descripción disponible'}</p>
              <p className="mt-1"><strong>Categoría:</strong> {categorias.find(c => c.id === macrosCalculados.alimento.categoria)?.nombre}</p>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Botones de acción */}
      <div className="flex gap-3 justify-end">
        <Button variant="outline" onClick={() => window.history.back()}>
          Cancelar
        </Button>
        <Button onClick={guardarAlimento} disabled={!macrosCalculados}>
          {alimentoInicial ? 'Actualizar Alimento' : 'Registrar Alimento'}
        </Button>
      </div>

      {/* Alimentos frecuentes */}
      <Card>
        <CardHeader>
          <CardTitle>Alimentos Frecuentes</CardTitle>
          <CardDescription>
            Accesos rápidos a alimentos comúnmente consumidos
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-2">
            {['pollo-pechuga', 'arroz-blanco', 'huevo-entero', 'platano', 'avena', 'yogur-griego', 'brocoli', 'almendras'].map(alimentoId => {
              const alimento = alimentosBase.find(a => a.id === alimentoId)
              if (!alimento) return null
              
              return (
                <Button
                  key={alimentoId}
                  variant="outline"
                  size="sm"
                  className="h-auto p-2 text-left"
                  onClick={() => {
                    setAlimentoSeleccionado(alimentoId)
                    setTimeout(calcularYMostrarMacros, 100)
                  }}
                >
                  <div>
                    <div className="font-medium text-xs">{alimento.nombre}</div>
                    <div className="text-xs text-gray-500">{alimento.calorias} cal/100g</div>
                  </div>
                </Button>
              )
            })}
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
